@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.HeaderLink {
    color: #ffffff;
    text-decoration: none;
}

.HeaderLink:hover {
    color: #ffffff;
    text-decoration: none;
}

.Header {
    padding: 5vh 5vh 5vh 3vh;
    max-width: 100vw;
    /* max-height: 20vh;     */
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    font-size: 16pt;
    background: #002A3B;
    border-bottom: 1px dashed skyblue;
}

.HeaderItem {
    text-decoration: none;
    color: #ffffff; 
    font-weight: 400;
    padding: 0vh 10vh 0vh 5vh;
    /* background: coral; */
}
.About {
    background: #002A3B;
    color: #FFEAd2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 70vh;
    max-width: 100vw;
}

.Greeting {
    padding: 0vh 10vw 2.5vh 5vh;
    font-size: 36pt;
    font-weight: medium;
    letter-spacing: -0.20rem;
    color: #ffffff;
    display: block;
}

.Intro {
    padding: 2.5vh 10vw 5vh 5vh;
    text-align: left;
    font-size: 16pt;
    max-width: 100vw;
    display: block;
}
.ProjectContainer {
    padding: 5px;
}

.ProjectName a {
    color: #002A3B;
    text-decoration: none;
}

.ProjectName a:hover {
    color: #002A3B;
    text-decoration: none;
}

.ProjectName a::after {
    content: " ðŸ –";
    font-family: "Times New Roman";
}

.ProjectContainer {
    background: #002A3B;
    max-height: 300vh;
    max-width: 100vw;
}

