.CounterPanel span {
    display: block;
    padding: 2.5vh;
    text-align: center;
    letter-spacing: -0.125rem;
}

.Counter {
    font-family: 'Karla';
    font-size: 36pt;
    color: #ffffff;
}

.ContentPanel {
    /* height: 80vh; */
    max-height: 100vh;
    background: #ffEAD2;
    color: #002A3B;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    /* border-bottom: 1px dashed #002A3B; */
}

.ContentPanel div {
    padding: 2.5vh;
}

.BottomRow {
    max-height: 100vh;
    max-width: 100vw;
    padding: 2.5vh;
    color: #ffffff;
}

.ProjectYear {
    font-size: 16pt;
}

.ProjectName {
    letter-spacing: -0.250rem;
    font-size: 36pt;
    font-weight: bold;
    color: #002A3B;
}

.ProjectLink {
    font-weight: 400;
    font-size: 12pt;
}

.ProjectDescription {
    padding: 2.5vh;
    font-size: 16pt;
}

.ProjectDetails {
    font-size: 18pt;
    font-family: 'Crimson Text';
    max-height: 80vh;
    padding: 2.5vh;
    margin-bottom: 2.5vh;
    text-align: left;
}

.ProjectDetailsButton {
    outline: none;
    background: #ffffff50;
    border-radius: 5px;
    font-weight: bold;
    outline: none !important;
    border: none;
    padding: 5px;
    font-size: 12pt;
    font-weight: bold;
    margin: 2.5vh 0vh;
}

.BottomRowProjectDetailsHeader {
    font-size: 12pt;
    color: skyblue;
    font-weight: bold;
}

.BottomRowProjectTagsHeader {
    font-size: 12pt;
    color: skyblue;
    font-weight: bold;
}

.Tag {
    margin: 2.5vh 1vh 2.5vh 2.5vh;
    background: #ffffff30;
    padding: 1vh;
    border-radius: 5px;
}
.App {
	max-width: 100vw;
	font-family: "Karla";
	background: #002A3B;
}
